<template>
  <div ref="wrapper" class="scroll-wrapper" :style="wrapperStyle">
    <div class="scroll-content" :style="contentStyle" ref="content">
      <slot></slot>
      <div class="tip-msg" v-show="showTip">
        <pulling-word v-show="!inPullUp&&dataList.length>0" :loadingWord="beforePullUpWord" ></pulling-word>
        <loading v-if="inPullUp" :loadingWord='PullingUpWord'></loading>
      </div>
    </div>

    <transition name="pulldown">
      <loading class="pulldown" v-if="inPullDown" :loadingWord='PullingDownWord'></loading>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import Loading from './loading.vue'
import PullingWord from './pullingWord.vue'
import * as CONSTANT from './constant'

const PullingUpWord = CONSTANT.LOADING_TIP
const beforePullUpWord = CONSTANT.LOADING_MORE
const PullingDownWord = CONSTANT.LOADING_MSG

export default {
  name: 'scrollWrapper',
  props: {
    /**
     * 
     */
    dataList: {
      type: Array,
      default: function () {
        return []
      }
    },
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 3
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    scrollY: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /*  */
    scrollbar: {
      type: Boolean,
      default: true
    },
    /*  */
    bounce: {
      type: [Boolean, Object],
      default: true
    },
    /**
     * 
     */
    pullDownRefresh: {
      type: null,
      default: false
    },
    /**
     *  false true  Object 
     * threshold
     */
    pullUpLoad: {
      type: null,
      default: false
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 20
    },
    /**
     * 
     */
    wrapperBgColor: {
      type: String,
      default: '#F9F9F9'
    },
    /**
     * 
     */
    contentBgColor: {
      type: String,
      default: '#fff'
    },
    /**
     * ID
     */
    eventId: {
      type: String,
      default: null
    },
    /**
     * 'vertical''horizontal'
     */
    eventPassthrough: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      scroll: null,
      inPullUp: false,
      inPullDown: false,
      beforePullUpWord,
      PullingUpWord,
      PullingDownWord,
      continuePullUp: true
    }
  },
  computed: {
    showTip () {
      if (this.scroll) {
        return this.scroll.maxScrollY < 0 && this.pullUpLoad
      }
      return false
    },
    wrapperStyle () {
      return {backgroundColor: this.wrapperBgColor}
    },
    contentStyle () {
      let contentStyle = {backgroundColor: this.contentBgColor}
      if (this.scrollX) {
        contentStyle.display = 'inline-block'
      }
      return contentStyle
    }
  },
  created () {
    if (this.eventId) {
      this.$eventBus.$on('init-scroll-' + this.eventId, holdPosition => {
        this.initScroll(holdPosition)
      })
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.initScroll()
    })
  },
  destroyed () {
    if (this.eventId) {
      this.$eventBus.$off('init-scroll-' + this.eventId)
    }
  },
  methods: {
    /**
     * 
     * holdPosition false, 
     */
    initScroll (holdPosition = false) {
      console.log('initScroll')
      // 
      let isScrollFlag = this.scrollbar
      if (this.scrollbar) { // 
        isScrollFlag = this.isNeedScroll()
      }

      // 
      let startX = 0
      let startY = 0
      if (this.scroll) {
        // 
        if (holdPosition) {
          startX = this.scroll.x
          startY = this.scroll.y
        }
        // 
        this.scroll.destroy()
      }

      // 
      this.scroll = new BScroll(this.$refs.wrapper, {
        startX,
        startY,
        scrollX: this.scrollX,
        scrollY: this.scrollY,
        probeType: this.probeType,
        click: this.click,
        pullDownRefresh: this.pullDownRefresh,
        pullUpLoad: this.pullUpLoad,
        scrollbar: isScrollFlag,
        bounce: this.bounce,
        eventPassthrough: this.eventPassthrough
      })
      // 
      this.scroll.on('pullingUp', () => {
        if (this.continuePullUp) {
          this.beforePullUp()
          this.$emit('onPullUp', CONSTANT.PULLUP_STATE)
        }
      })
      // 
      this.scroll.on('pullingDown', () => {
        this.beforePullDown()
        this.$emit('onPullDown', CONSTANT.PULLDOWN_STATE)
      })

      // 
      if (this.listenScroll) {
        let me = this
        this.scroll.on('scroll', (pos) => {
          me.$emit('scroll', pos)
        })
      }
    },
    beforePullUp () {
      this.PullingUpWord = PullingUpWord
      this.inPullUp = true
    },
    beforePullDown () {
      this.disable()
      this.inPullDown = true
    },
    finish (type) {
      this['finish' + type]()
      this.enable()
      this['in' + type] = false
    },
    disable () {
      this.scroll && this.scroll.disable()
    },
    enable () {
      this.scroll && this.scroll.enable()
    },
    refresh () {
      this.scroll && this.scroll.refresh()
    },
    finishPullDown () {
      this.scroll && this.scroll.finishPullDown()
    },
    finishPullUp () {
      this.scroll && this.scroll.finishPullUp()
    },
    /*  */
    isNeedScroll () {
      let wrapHeight = this.$refs.wrapper.offsetHeight // 
      let contentHeight = this.$refs.content.offsetHeight // 
      // console.log('wrapHeight=', wrapHeight, ', contentHeight=', contentHeight)
      return wrapHeight < contentHeight
    }
  },
  components: {
    Loading,
    PullingWord
  }
}
</script>
<style lang="scss" scoped>
@import '@/assets/style/variables.scss';

.scroll-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
}
.scroll-content {
  min-height: 100%;
  &:before {
    content: '';
    display: table;
  }
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}
.pulldown-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all;
}
.pullup-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px 0;
}
.pulldown {
  position: absolute;
  top: 0;
  left: 0;
}
.pulldown-enter-active {
  transition: all 0.2s;
}
.pulldown-enter, .pulldown-leave-active {
  transform: translateY(-100%);
  transition: all 0.2s;
}
.tip-msg {
  position: absolute;
  width: 100%;
  text-align: center;
}
</style>
<style>
.bscroll-vertical-scrollbar{
  width: 7px !important;
}
</style>



