<template>
  <DetailShell barTitle="" scrollBgColor="#F9F9F9">
    <div class="radio-wrap">
      <Radio title=":" :options="radioOptions" v-model="bank" @change="doChange"></Radio>
      <div class="result">{{bank}}</div>
    </div>
  </DetailShell>
</template>
<script>
import DetailShell from '@/components/modules/app/DetailShell.vue'
import Radio from '@/components/radio/index.vue'
export default {
  name: 'RadioDemo',
  components: {
    DetailShell,
    Radio
  },
  data () {
    return {
      bank: '03',
      radioOptions: [
        { value: '01', text: '' },
        { value: '02', text: '' },
        { value: '03', text: '' },
        { value: '04', text: '' },
        { value: '05', text: '', disabled: true }
      ]
    }
  },
  methods: {
    doChange (val) {
      console.log('changevalue = ', val)
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@/assets/style/variables.scss';

.radio-wrap{
  font-size: 36px;
  line-height: 90px;
  .result{
    margin-top: 40px;
    padding: 0 40px;
  }
}
</style>



